<script setup lang="ts">
import { Ref, computed, defineProps, onBeforeMount, onMounted, ref, watchEffect } from 'vue';
import HeaderComponent from '../components/page/login/components/HeaderComponent.vue';
import router from '@/router';
import UserService from '../components/page/login/helper/homeService';
import store from '@/store';
import { jwtDecode } from 'jwt-decode';
let user = ref('');
let role = ref('');
const token = store.getters.token;
onMounted(() => {
    if (token) {
        const decodedToken: { sub: string; role: string } = jwtDecode(token);
        console.log('decodedjwt', decodedToken);
        if (decodedToken.sub) {
            user.value = decodedToken.sub;
        }
        if (decodedToken.role) {
            role.value = decodedToken.role;
        }
    }
});
async function irMedicos() {
    try {
        const response = await UserService.getMedicosBoard();
        console.log('respuesta', response);
        router.push({ name: 'medicos' });
    } catch (error) {
        console.error('Error :', error);
    }
}

async function irPacientes() {
    try {
        const response = await UserService.getPacientesBoard();
        console.log('respuesta', response);
        router.push({ name: 'pacientes' });
    } catch (error) {
        console.error('Error :', error);
    }
}

